body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: "Inter";

  background-color: #fefaf0;
  overflow-x: hidden;
}
.Diagonal {
  width: 0;
  height: 0;
  border-left: 100vw solid transparent;
  border-bottom: 10vw solid #fefaf0;
  margin-top: calc(60vh - 9.9vw);
  position: relative;
  z-index: 1;
  margin-bottom: 0;
}
.Loading {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}
/* HEADER CSS */

.Header {
  background-image: linear-gradient(to bottom right, #e1c16e, #ffefca);
  height: 60vh;
  display: flex;
  align-items: center;

  border: none;

  position: fixed;
  top: 0;
  width: 100%;
}
.Header .Text {
  margin-left: 15vw;
}
.Header h1 {
  margin-top: 5vh;
  font-weight: 500;
  font-size: 4rem;
  margin-bottom: 0;
}
.Header span {
  font-size: 1.5rem;
  font-weight: 500;
}
.Container {
  width: fit-content;
}
/*About CSS */

.About {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  padding-left: 15vw;
  padding-right: 15vw;
  width: 70vw;
  font-size: 1.25rem;
  background-color: #fefaf0; /*#FEFAF0*/
  position: relative;
  z-index: 2;
}
.About h2 {
  padding-top: 50px;
  position: relative;
  z-index: 3;
}
.About h2,
.About p {
  width: 600px;
  margin-top: 0;
}
#AboutMe::before {
  background-color: #008896;
  position: absolute;
  content: "";
  height: 0.7rem;
  width: 9.5rem;
  z-index: -1;
  bottom: 0;
  transform: translate(-0.3rem, -0.1rem);
}
#MySkills::before {
  background-color: #008896;
  position: absolute;
  content: "";
  height: 0.7rem;
  width: 8.75rem;
  z-index: -1;
  bottom: 0;
  transform: translate(-0.3rem, 0rem);
}

.About .LogoImages img {
  height: 80px;
  width: auto;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;
}
#javaIcon {
  margin-right: -5px;
  margin-left: -5px;
}
.About .LogoImages {
  display: flex;
  width: fit-content;
  justify-content: center;
}

.imageAndSocials {
  float: right;
  position: relative;
  bottom: 400px;
}
.Socials img {
  width: 60px;
  height: auto;
  cursor: pointer;
  margin-left: 10px;
  margin-right: 10px;
}
.Socials {
  position: relative;
  top: 15px;
  display: flex;
  justify-content: center;
  z-index: 0;

  animation: slideFromTop 0.5s;
}
#portrait {
  width: 400px;
  height: auto;
  border-radius: 15px;
  max-width: 400px;
  position: relative;
  z-index: 1;
}
.Content {
  width: 600px;
  background-color: #fefaf0;
  animation: transitionIn 1s;
}
/* PROJECTS CSS*/
.Projects {
  margin-top: -150px;
  text-align: center;
  position: relative;
  z-index: 3;
  background-color: #fefaf0;
}
.Projects h2 {
  font-size: 4rem;
  position: relative;
}
.Projects h2::before {
  background-color: #008896;
  position: absolute;
  content: "";
  height: 1.75rem;
  width: 24.2rem;
  z-index: -1;
  bottom: 0;
  transform: translate(-0.5rem, 0.1rem);
}

.Projects img {
  width: 34vw;
  height: 17vw;
  object-fit: cover;
  margin-bottom: 1vw;
}
.overlay {
  position: absolute;
  bottom: 0;
  left: 100%;
  right: 0;
  background-color: #e1c16e;
  overflow: hidden;
  width: 0;
  height: 100%;
  transition: 0.5s ease !important;
}
.Slide {
  position: relative;
  width: fit-content;
  height: 17vw;
  margin-bottom: 1vw;
  padding: 0;
}
.Slide .text {
  color: Black;
  font-size: 2rem;
  font-weight: 600;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  white-space: nowrap;
}
.Slide:hover .overlay {
  width: 100%;
  left: 0;
}
.Images {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 15vw;
  margin-right: 15vw;
}
/*PROJECT SLIDE CSS*/
:root {
  --delay: 0;
}
.Slide {
  transform: translateX(-150%);
  opacity: 0;
  transition: all 1s;
  transition-delay: var(--delay);
}
.slide-in {
  transform: translateX(0) !important;
  opacity: 1 !important;
  transition: all 1s;
  transition-delay: var(--delay);
}

@media (max-width: 1500px) {
  .Header h1 {
    width: 390px;
  }
  .About h2,
  .About p {
    width: 300px;
  }
  .Content {
    width: 300px;
  }
  .Projects {
    margin-top: -100px;
  }
}
@media (max-width: 1200px) {
  .Header .Text {
    margin-left: 5vw;
  }
  .About {
    padding-left: 5vw;
    padding-right: 5vw;
    width: 90vw;
  }
  .Slide {
    height: 21vh;
  }
  .Images {
    margin-left: 5vw;
    margin-right: 5vw;
  }
  .Projects img {
    width: 42vw;
    height: 21vh;
  }
}
@media (max-width: 850px) {
  .Header {
    justify-content: center;
    text-align: center;
  }
  .Header .Container {
    display: inline;
  }
  .Header .Text {
    position: relative;
    bottom: 100px;
    margin-left: 0vw;
  }
  .About {
    flex-direction: column-reverse;
    justify-content: center;
    justify-items: center;
    align-items: center;
  }

  .Content {
    width: 80vw;
    margin-right: 5vw;
    position: relative;
    bottom: 200px;
  }
  .About h2,
  .About p {
    width: 80vw;
  }
  .imageAndSocials {
    bottom: 200px;
  }
  #portrait {
    width: 80vw;
    margin-left: 10vw;
    margin-right: 10vw;
  }
  .Socials img {
    width: 10vw;
    max-width: 60px;
  }

  .About .LogoImages {
    flex-wrap: wrap;
  }

  .Images {
    flex-direction: column;
    align-items: center;
  }
  .Slide {
    height: 45vh;
  }
  /* .Images{
        margin-left: 5vw;
        margin-right: 5vw;
    } */
  .Projects img {
    width: 90vw;
    height: 45vh;
  }
  .Projects {
    margin-top: -150px;
  }
  .Projects .text {
    font-size: 5vw;
  }
  .show {
    opacity: 1;
  }
}

@keyframes transitionIn {
  from {
    opacity: 0;
    transform: rotateX(-10deg);
  }
  to {
    opacity: 1;
    transform: rotateX(0);
  }
}

@keyframes slideFromTop {
  from {
    opacity: 0;
    transform: translateY(-300px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
